<template>
  <div
    class="validate page-container text-center d-flex align-items-center justify-content-center"
  >
    <div class="text-left">
      <p class="fs-24">
        Please contact
        <a
          class="text-primary fw-5"
          href="mailto:kelei1992@outlook.com"
          target="_blank"
          >kelei1992@outlook.com</a
        ><br />to request access.
      </p>
      <p class="msg fw-5 fs-16" v-show="errorMsg">{{ errorMsg }}</p>
      <form class="d-inline-block input-wrap" v-on:submit="validatePsd">
        <input
          class="mb-6 pwd"
          type="password"
          placeholder="Enter password"
          v-model="password"
        />
        <img
          @click="validatePsd"
          loading="lazy"
          class="social-icon"
          :src="hashUrl('arrow.svg')"
        />
      </form>
    </div>
  </div>
</template>
<script>
export default {
  name: "ValidatePage",
  components: {},
  data() {
    return {
      password: "",
      errorMsg: "",
    };
  },
  methods: {
    validatePsd() {
      let res = this.jymm()(this.password);
      if (res) {
        sessionStorage.setItem("password", this.password);
        if (this.query) {
          this.$router.replace(`/project/${this.query}`);
        } else {
          this.$router.replace(`/`);
        }
      } else {
        this.errorMsg = "Invalid password";
        setTimeout(() => {
            this.errorMsg = '';
        }, 2000);
      }
    },
  },
  mounted() {
    const {
      query: { projectName },
    } = this.$route;
    this.query = projectName;
  },
};
</script>
  
<style lang="scss" scoped>
.validate {
  color: #fff;
}
.input-wrap {
  position: relative;
  .social-icon {
    position: absolute;
    right: 0;
    top: 0;
    width: 24px;
    height: 24px;
    padding: 18px;
    cursor: pointer;
    box-sizing: content-box;
    z-index: 20;
    transition: all linear 0.3s;
    &:hover {
      transform: rotateZ(45deg);
    }
  }
}
.pwd {
  margin: 0 0 100px;
  display: block;
  width: 300px;
  height: 60px;
  color: #fff;
  border-radius: 30px;
  padding: 0 60px 0 24px;
  line-height: 60px;
  font-size: 20px;
  background-color: transparent;
  outline: none;
  box-shadow: 0 0 1px 1px #fff inset;
}
.msg {
  color: red;
  animation: fadeOut 2s linear;
}

@keyframes fadeOut {
    from {
        opacity: 1;
    }
    to {
        opacity: 0.2;
    }
}
</style>