const tag = "Ireland"

export const PROJECT_INDICES = ["miOffice", "corporatePayment", "miTravel", "afterSale", "hiui", "miCost", "graphicDesign"]

export const SLIDE_INDICES = {
    "approval": 2,
    "design": 1
}

export const jymm = () => {
    var psmm = tag + props + children;
    function f1(input = '') {
        return input == psmm;
    }
    return f1;
}

